import interfaces from '@/utils/interfaces.js'
export default {
	data() {
		return {
			title: 'uploadFile',
			imageSrc: ''
		}
	},
	props:{
		imagesrc:{
			type: String,
			default: ''
		}
	},
	onUnload() {
		this.imageSrc = '';
	},
	methods: {
		chooseImage: function() {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album'],
				success: (res) => {
					var imageSrc = res.tempFilePaths[0]
					uni.uploadFile({
						url: interfaces.getUploadData,
						filePath: imageSrc,
						fileType: 'image',
						name: 'data',
						success: (res) => {
							uni.showToast({
								title: '上传成功',
								icon: 'success',
								duration: 1000
							})
							this.imageSrc = imageSrc
						},
						fail: (err) => {	
							uni.showModal({
								content: err.errMsg,
								showCancel: false
							});
						}
					});
				},
				fail: (err) => {

					uni.getSetting({
						success: (res) => {
							let authStatus = res.authSetting['scope.album'];
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: 'Hello uni-app需要从您的相册获取图片，请在设置界面打开相关权限',
									success: (res) => {
										if (res.confirm) {
											uni.openSetting()
										}
									}
								})
							}
						}
					})

				}
			})
		}
	}
}
